import React, { Fragment } from "react";
import { useState, useEffect } from "react";
import tr7TradingBot from "../Model/fetch-tr7-trading-bot.js";
import { CCard, CCardHeader, CCardBody, CRow, CCol } from "@coreui/react";
const TR7BotAccount = (props) => {
  const [message, $message] = useState(null);
  async function fetchSomeBotData() {
    const response = await tr7TradingBot.fetchAccountInfo();
    return response;
  }
  useEffect(() => {
    fetchSomeBotData().then((response) => $message(response));
    const id = setInterval(() => {
      fetchSomeBotData().then((response) => $message(response));
    }, 1500);

    return () => clearInterval(id);
  }, []);

  // return (
  //   <CRow>
  //     <h1>Account Information</h1>
  //     <div className="">{JSON.stringify(message)}</div>;
  //   </CRow>
  // );
  if (!message) {
    return <>Loading...</>;
  } else {
    // console.log(message.accounts);
    // console.log(message.balance);
    return (
      <>
        <h1>Account Information</h1>
        <CRow>
          {message.accounts.map((account, index) => {
            console.log(account);
            delete account.positions;
            delete account.assets;
            const keys = Object.keys(account);

            return (
              <CCol xl={6}>
                <CCard className="mb-4">
                  <CCardHeader>Account {index + 1}</CCardHeader>
                  <CCardBody>
                    <p>Detail information of the account</p>
                    <table className="table">
                      <thead>
                        <tr>
                          <th>Field</th>
                          <th>Value</th>
                        </tr>
                      </thead>
                      <tbody>
                        {keys.map((key, index) => {
                          return (
                            <tr>
                              <td>
                                <p>
                                  <code className="highlighter-rouge">
                                    {key}
                                  </code>
                                </p>
                              </td>
                              <td>
                                <span className="h6">
                                  {JSON.stringify(account[key])}
                                </span>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </CCardBody>
                </CCard>
              </CCol>
            );
          })}
          {/* <CCol xl={6}>
            <CCard className="mb-4">
              <CCardHeader>Account xx</CCardHeader>
              <CCardBody>
                <p>Detail information of the account</p>
                <table className="table">
                  <thead>
                    <tr>
                      <th>Field</th>
                      <th>Value</th>
                    </tr>
                  </thead>
                  <tbody></tbody>
                </table>
              </CCardBody>
            </CCard>
          </CCol> */}
        </CRow>
      </>
    );
  }
};

export default TR7BotAccount;
